.filtration-container {
  padding: 30px;
  max-width: 100%;
  margin: 0 auto;
}

.content-with-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.table-container {
  flex: 1;
  margin-right: 40px;
}

.settings-panel {
  min-width: 220px;
  padding: 16px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background: #f9f9f9;
}

.settings-panel h3 {
  margin-bottom: 12px;
}

.checkbox-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.no-data {
  margin-top: 20px;
  color: #888;
  text-align: center;
}

.description {
  font-size: 16px;
  margin-bottom: 12px;
  line-height: 1.6;
  color: #333;
}

.features-list {
  margin-bottom: 20px;
  padding-left: 20px;
  color: #555;
}

.features-list li {
  margin-bottom: 6px;
}

/* Общий стиль фиолетовой кнопки */
.purple-button {
  color: white;
  border: 2px solid #8707ff;
  border-radius: 10px;
  padding: 10px 25px;
  background: transparent;
  transition: box-shadow 0.3s ease;
}

.purple-button:hover {
  box-shadow: 0 0 10px #8707ff;
}

/* Для активного состояния */
.purple-button:active {
  box-shadow: 2px 2px 15px #8707ff inset;
}

/* Стилизация FloatButton через обёртку */
.floating-btn {
  background-color: transparent !important;
  border: 2px solid #8707ff !important;
  color: #8707ff !important;
}
